import React from 'react'
import { Button, Modal, Input, Icon } from 'antd'
import Common from '/page/common.jsx'

import CertificateForm from '/page/certificate/certificateForm.jsx'
import PicturesWall from '/page/certificate/PicturesWall.jsx'

class Certificate extends Common{

	async componentWillMount () {
		this.search()
	}

	state = Object.assign({}, this.state, {
		search: Object.assign({}, this.state.pageconf),
		columns: [{
			title: '证书全称',
			dataIndex: 't1'
		},{
			title: '证书中文简称',
			dataIndex: 't2'
		},{
			title: '证书英文简称',
			dataIndex: 't3'
		},{
			title: '有效截止日期',
			dataIndex: 't4'
		},{
			title: '发证机构',
			dataIndex: 't5'
		},{
			title: '证书数量',
			dataIndex: 't6'
		}],
		modalConf: {visible: false, item: {}},
		picmodelConf: {visible: false, item: {}}
	})

	search = async _ => {
		let data = [{key: 1, t1: '中国十佳IT服务商', t2: '十佳服务商', t3: 'SJFWS', t4: '2019年5月5日', t5: '中国IT协会', t6: '3'},
		{key: 2, t1: '中国十佳IT服务商', t2: '十佳服务商', t3: 'SJFWS', t4: '2019年5月5日', t5: '中国IT协会', t6: '3'},
		{key: 3, t1: '中国十佳IT服务商', t2: '十佳服务商', t3: 'SJFWS', t4: '2019年5月5日', t5: '中国IT协会', t6: '3'},
		{key: 4, t1: '中国十佳IT服务商', t2: '十佳服务商', t3: 'SJFWS', t4: '2019年5月5日', t5: '中国IT协会', t6: '3'},
		{key: 5, t1: '中国十佳IT服务商', t2: '十佳服务商', t3: 'SJFWS', t4: '2019年5月5日', t5: '中国IT协会', t6: '3'},
		{key: 6, t1: '中国十佳IT服务商', t2: '十佳服务商', t3: 'SJFWS', t4: '2019年5月5日', t5: '中国IT协会', t6: '3'},
		{key: 7, t1: '中国十佳IT服务商', t2: '十佳服务商', t3: 'SJFWS', t4: '2019年5月5日', t5: '中国IT协会', t6: '3'},
		{key: 8, t1: '中国十佳IT服务商', t2: '十佳服务商', t3: 'SJFWS', t4: '2019年5月5日', t5: '中国IT协会', t6: '3'}]
		this.setState({
				tabledata: data, 
				loading: false,
				pagination: Object.assign({}, this.state.pagination, {
					total: 8,
					current: 0
				})
			})
	}

	delete = _ => {
		this.handleOk(deletesupplier, 'id', '删除')
	}

	done = async _ => {
		let modalConf = {visible: false, item: {}}
		await this.setState({ modalConf })
		this.research()
	}

	openPicModel = _ => {
		this.setState({picmodelConf: {visible: true}})
	}

	renderSearch = _ => <div className="mgrSearchBar">
		<Input 
		value={this.state.search.name}
		suffix={this.state.search.name ? <Icon 
		type="close-circle" 
		onClick={_ => this.changeSearch({name: ''})} /> : null}
		onChange={e => this.changeSearch({name: e.target.value})}
		style={{width: 250}}
		addonBefore="名称" placeholder="名称" />
		<Button 
		onClick={this.search}
		type="primary" icon="search">搜索</Button>
		<Button 
		onClick={this.research}
		type="primary" icon="retweet">重置</Button>
		<Button 
		onClick={_ => this.addmodal('modalConf', '添加')}
		type="primary" icon="plus">添加</Button>
		<Button 
		onClick={_ => this.editmodal('modalConf', '编辑')}
		type="primary" icon="edit">修改</Button>
		<Button 
		onClick={this.changemodel}
		type="primary" icon="delete">删除</Button>
		<Button 
		onClick={this.openPicModel}
		type="primary" icon="picture">影像</Button>
	</div>

	rendermodal = _ => [<CertificateForm 
		onCancel={_ => this.cancelform('modalConf')}
		done={this.done}
		config={this.state.modalConf} />,
		<PicturesWall onCancel={_ => this.cancelform('picmodelConf')}
		config={this.state.picmodelConf} />,
		<Modal title="信息"
		  visible={this.state.visible}
		  onOk={this.delete}
		  mask={false}
		  width={400}
		  onCancel={this.changemodel}
		  okText="确认"
		  cancelText="取消"
		>
			<p>是否删除？</p>
		</Modal>]

}

export default Certificate


