import React, { Component } from 'react'
import { Modal, Input, Form, message, DatePicker, TreeSelect } from 'antd'
const FormItem = Form.Item

const treeData = [{
  title: '等级证书',
  value: '0-0',
  key: '0-0',
  children: [{
    title: '操作证书',
    value: '0-0-1',
    key: '0-0-1',
  }, {
    title: '运维证书',
    value: '0-0-2',
    key: '0-0-2',
  }],
	}, {
	  title: '测试证书',
	  value: '0-1',
	  key: '0-1',
	}];
class CertificateForm extends Component{
	async componentWillReceiveProps (nextprops) {
		if (nextprops.config != this.props.config && nextprops.config.visible) {
			if (nextprops.config.type != 'edit') {
				this.props.form.resetFields()
			} else {
				let mes = nextprops.config.item
				let config = {}
				let fieldsList = ['name', 'code', 'linkName', 'linkPhone']
				fieldsList.forEach(val => {
					config[val] = {value: mes[val]}
				})

				this.props.form.setFields(config)
			}
		}
	}

	state = {
		rules: [{
			label: '分类',
			key: 'type',
			option: {rules: [{required: true, message: '请选择分类!'}]},
			render: _ =>  <TreeSelect
        style={{ width: 240 }}
        dropdownStyle={{ maxHeight: 300, overflow: 'auto' }}
        treeData={treeData}
        placeholder="选择分类"
        treeDefaultExpandAll
      />
		},{
			label: '证书全称',
			key: 't1',
			option: {rules: [{required: true, message: '请输入名称!'},{max: 50, message: '最多输入50个字符'}]},
			render: _ => <Input style={{width: 200}}/>
		}
		,{
			label: '证书中文简称',
			key: 'code',
			option: {rules: [{required: true, message: '请输入编号!'},{max: 50, message: '最多输入50个字符'}]},
			render: _ => <Input style={{width: 200}}/>
		},{
			label: '证书英文简称',
			key: 'linkName',
			option: {rules: [{max: 50, message: '最多输入50个字符'}]},
			render: _ => <Input style={{width: 200}}/>
		},{
			label: '证书有效截止日期',
			key: 'linkPhone',
			option: {rules: [{max: 20, message: '最多输入20个字符'}]},
			render: _ => <DatePicker placeholder="选择日期" />
		},{
			label: '证书颁发机构',
			key: 'linkPhone',
			option: {rules: [{max: 20, message: '最多输入20个字符'}]},
			render: _ => <Input style={{width: 200}}/>
		},{
			label: '证书数量',
			key: 'linkPhone',
			option: {rules: [{max: 20, message: '最多输入20个字符'}]},
			render: _ => <Input style={{width: 200}}/>
		}],
		loading: false,
		lock: false
	}

	handleFetch = params => {
		if (this.props.config.type == 'edit') {
			params.id = this.props.config.item.id
			return updatesupplier(params)
		}
		return addsupplier(params)
	}

	handleOk = async _ => {
		if (!this.state.lock) {
			await this.setState({lock: true})
			this.props.form.validateFieldsAndScroll(null, {}, (err, val) => {
				if (!err || !Object.getOwnPropertyNames(err).length) {
					let params = Object.assign({}, val)

					this.handleFetch(params)
					.then(res => {
						if (res.code == 200 || res === true) {
							message.success('操作成功')
							this.props.done()
						}
						this.setState({lock: false})
					})
				} else {
					this.setState({lock: false})
				}
			})
		}
	}

	render = _ => {
		const { getFieldDecorator } = this.props.form
		const formItemLayout = {
			labelCol: {
				xs: { span: 24 },
				sm: { span: 8 }
			},
			wrapperCol: {
				xs: { span: 24 },
				sm: { span: 16 }
			}
		}

		return <Modal title={this.props.config.title}
		onOk={this.handleOk}
		visible={this.props.config.visible}
		confirmLoading={this.state.loading}
		onCancel={this.props.onCancel}
		okText="提交"
		style={{top: 50, marginBottom: 100}}
		cancelText="取消">
			<Form>
				{this.state.rules.map((item, index) => 
					<FormItem key={index} {...formItemLayout} label={item.label}>
						{getFieldDecorator(item.key, item.option)(item.render())}
					</FormItem>
				)}
			</Form>
		</Modal>
	}
}

export default Form.create()(CertificateForm)


